  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.stack__item.loader{
  width: 500px;
  height: 290px;
  opacity: 0;
  z-index: -100;
  margin: auto;
  position: absolute;
  left: 50%;
  margin-left: -250px;
  -webkit-transition: ease all 0.3s;
  transition: ease all 0.3s;
}
.stack__item.loader.active{
  z-index: 1001;
  opacity: 1;
}
#stack_yuda{
  -webkit-transition: ease all 0.5s;
  transition: ease all 0.5s;
  opacity: 1;
}
#stack_yuda.inactive{
  opacity: 0;
  -webkit-transition: ease all 0.5s;
  transition: ease all 0.5s;
}
.active{
 -webkit-animation-duration: .25s;
    animation-duration: .25s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-name: fade-content;
    animation-name: fade-content;
    opacity: 0;
    -webkit-transform: scale(.97) translateY(5px);
    transform: scale(.97) translateY(5px);
    -webkit-transition: .5s cubic-bezier(.2,.5,0,1.1);
    transition: .5s cubic-bezier(.2,.5,0,1.1);
}
@-webkit-keyframes fade-content {
  to {
    opacity:1;
    -webkit-transform:none;
    transform:none
  }
}
@keyframes fade-content {
  to {
    opacity:1;
    -webkit-transform:none;
    transform:none
  }
}
